@use '../foundation/_variable' as *;

.l-footer {
    background-color: $color-base;
    &__content {
        display: flex;
        padding: 0 2rem;
        gap: 3rem;
        align-items: flex-end;
        justify-content: space-between;
        @include breakpoint(small) {
            display: block;
            position: relative;
            width: 100%;
            height: auto;
        }
    }
    &__logo {
        height: auto;
    }
    &__nav {
        margin-top: 2.5rem;
        @include breakpoint(small) {
            position: absolute;
            left: 50%;
            top: 85%;
            transform: translateX(-50%);
        }
    }
    &__nav ul {
        display: flex;
        font-weight: bold;
        width: 14rem;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        margin-bottom: 1.5rem;
        font-size: 1.2rem;
        @include breakpoint(small) {
            flex-direction: row;
            gap: 1.5rem;
            margin-bottom: 0;
            font-size: 1.1rem;
            justify-content: center;
            width: 350rem;
            margin: 0 auto;
            color: $color-base;
        }
    }
        li {
            text-align: left;
    }
    small {
        background-color: $color-green;
        font-weight: normal;
        font-size: 1.3rem;
        font-family: $font-din;
        color: $color-base;
        text-align: center;
        display: block;
        height: 4rem;
        line-height: 4rem;
        @include breakpoint(small) {
            height: 7rem;
            padding-top: 3rem;
        }
    }
}