@use '../../foundation/_variable' as *;

.p-cta {
    padding-top: 5rem;
    background: linear-gradient($color-bg-cream, transparent 20%);
    @include breakpoint(small) {
    padding-top: 3rem;
    }
    &__bgBase {
        background: $color-base;
    }
    &__bgYellow {
        background: linear-gradient($color-bg-yellow,  transparent 20%);
    }
    &__heading img {
        margin: 0 auto;
        width: min(27rem, 42vw);
        height: auto;
    }
    &__img {
        margin-top: 2rem;
        @include breakpoint(small) {
            margin-top: 1rem;
        }
        & img {
            width: 100%;
            object-fit: contain;
            object-position: bottom;
        }
    }
    &__img__short {
        margin-top: -3.5rem;
        @include breakpoint(small) {
            margin-top: -3rem;
        }
    }
    &.u-pt__40 {
        padding-top: 4rem;
        @include breakpoint(small) {
            padding-top: 2rem;
        }
    }
}
