@font-face {
  font-family: "notosans";
  font-style: normal;
  font-weight: 400;
  src: url("../font/notosansNormal.woff") format("woff"), url("../font/notosansNormal.ttf") format("truetype");
}
@font-face {
  font-family: "notosans";
  font-style: medium;
  font-weight: 500;
  src: url("../font/notosansMedium.woff") format("woff"), url("../font/notosansMedium.ttf") format("truetype");
}
@font-face {
  font-family: "notosans";
  font-style: bold;
  font-weight: 700;
  src: url("../font/notosansBold.woff") format("woff"), url("../font/notosansBold.ttf") format("truetype");
}
@font-face {
  font-family: "notosans";
  font-style: black;
  font-weight: 900;
  src: url("../font/notosansBlack.woff") format("woff"), url("../font/notosansBlack.ttf") format("truetype");
}
@font-face {
  font-family: "zenkaku";
  font-style: normal;
  font-weight: 400;
  src: url("../font/zenkakuRegular.woff") format("woff"), url("../font/zenkakuRegular.ttf") format("truetype");
}
@font-face {
  font-family: "zenkaku";
  font-style: medium;
  font-weight: 500;
  src: url("../font/zenkakuMedium.woff") format("woff"), url("../font/zenkakuMedium.ttf") format("truetype");
}
@font-face {
  font-family: "zenkaku";
  font-style: bold;
  font-weight: 700;
  src: url("../font/zenkakuBold.woff") format("woff"), url("../font/zenkakuBold.ttf") format("truetype");
}
@font-face {
  font-family: "zenkaku";
  font-style: black;
  font-weight: 700;
  src: url("../font/zenkakuBlack.woff") format("woff"), url("../font/zenkakuBlack.ttf") format("truetype");
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.5;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  color: #663300;
  font-size: 1.6rem;
  font-family: "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", "DIN Alternate", "Kozuka Mincho Pro", sans-serif;
  min-width: 350px;
  margin: 0 auto;
  width: 100%;
  overflow-x: hidden;
  max-width: 640px;
}

img,
video,
object {
  max-width: 100%;
  height: auto;
  border: none;
  display: block;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

a:hover img {
  text-decoration: none;
  opacity: 0.8;
  transition: all 0.3s;
}

li {
  list-style: none;
}

.l-footer {
  background-color: #ffffff;
}
.l-footer__content {
  display: flex;
  padding: 0 2rem;
  gap: 3rem;
  align-items: flex-end;
  justify-content: space-between;
}
@media (max-width: 576px) {
  .l-footer__content {
    display: block;
    position: relative;
    width: 100%;
    height: auto;
  }
}
.l-footer__logo {
  height: auto;
}
.l-footer__nav {
  margin-top: 2.5rem;
}
@media (max-width: 576px) {
  .l-footer__nav {
    position: absolute;
    left: 50%;
    top: 85%;
    transform: translateX(-50%);
  }
}
.l-footer__nav ul {
  display: flex;
  font-weight: bold;
  width: 14rem;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
}
@media (max-width: 576px) {
  .l-footer__nav ul {
    flex-direction: row;
    gap: 1.5rem;
    margin-bottom: 0;
    font-size: 1.1rem;
    justify-content: center;
    width: 350rem;
    margin: 0 auto;
    color: #ffffff;
  }
}
.l-footer li {
  text-align: left;
}
.l-footer small {
  background-color: #009933;
  font-weight: normal;
  font-size: 1.3rem;
  font-family: "din-2014", "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  color: #ffffff;
  text-align: center;
  display: block;
  height: 4rem;
  line-height: 4rem;
}
@media (max-width: 576px) {
  .l-footer small {
    height: 7rem;
    padding-top: 3rem;
  }
}

.c-inner {
  max-width: 640px;
  margin: 0 auto;
  padding: 0 2rem;
}
@media (max-width: 576px) {
  .c-inner {
    padding: 0 1rem;
  }
}

.c-btn {
  width: min(90vw, 57rem);
  margin: -1rem auto 0.5rem;
  display: flex;
  justify-content: center;
}
.c-btn:hover {
  opacity: 0.8;
}
@media (max-width: 576px) {
  .c-btn {
    margin-top: 0;
  }
}

.c-sectionTitle {
  margin: 0 auto;
  text-align: center;
}
.c-sectionTitle__en {
  font-size: 2.1rem;
  font-family: "din-2014", "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  line-height: 1.2;
  letter-spacing: 0.1em;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 0.6rem;
}
.c-sectionTitle__jp {
  font-size: 6rem;
  font-weight: 900;
  display: inline-block;
  border-top: 2px solid #663300;
  line-height: 1.25;
}
@media (max-width: 576px) {
  .c-sectionTitle__jp {
    font-size: max(4.6rem, 10vw);
  }
}

.p-fv {
  width: 100%;
  max-width: 64rem;
  display: block;
}
.p-fv__height {
  margin-top: min(86rem, 124vw);
}

.p-cta {
  padding-top: 5rem;
  background: linear-gradient(#fdf7ed, transparent 20%);
}
@media (max-width: 576px) {
  .p-cta {
    padding-top: 3rem;
  }
}
.p-cta__bgBase {
  background: #ffffff;
}
.p-cta__bgYellow {
  background: linear-gradient(#fff7da, transparent 20%);
}
.p-cta__heading img {
  margin: 0 auto;
  width: min(27rem, 42vw);
  height: auto;
}
.p-cta__img {
  margin-top: 2rem;
}
@media (max-width: 576px) {
  .p-cta__img {
    margin-top: 1rem;
  }
}
.p-cta__img img {
  width: 100%;
  object-fit: contain;
  object-position: bottom;
}
.p-cta__img__short {
  margin-top: -3.5rem;
}
@media (max-width: 576px) {
  .p-cta__img__short {
    margin-top: -3rem;
  }
}
.p-cta.u-pt__40 {
  padding-top: 4rem;
}
@media (max-width: 576px) {
  .p-cta.u-pt__40 {
    padding-top: 2rem;
  }
}

.p-campaign {
  background-color: #fdf7ed;
  padding-bottom: 0.5rem;
}
.p-campaign__title {
  width: 28rem;
  text-align: center;
  margin: 0 auto;
  padding-top: 4rem;
  font-size: 2.4rem;
}
.p-campaign__titleJp {
  border-bottom: 2px solid #663300;
  font-weight: 900;
  font-family: "din-2014", "zenkaku", "Arial", "Yu Gothic", "Meiryo", sans-serif;
}
.p-campaign__titleEn {
  text-transform: uppercase;
  font-family: "din-2014", "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  letter-spacing: 0.1em;
  line-height: 1.1;
}
.p-campaign__heading {
  margin: 2.5rem auto 0;
  text-align: center;
  color: #ffffff;
  font-size: 3.6rem;
  max-width: 46.6rem;
  height: 11rem;
  font-weight: 900;
  line-height: 1.2;
  letter-spacing: -0.1em;
  background: url("../../assets/img/campaign-heading-bg@2x-8.png");
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 576px) {
  .p-campaign__heading {
    font-size: 7vw;
  }
}
.p-campaign__catch {
  margin: 2.8rem auto 0;
  max-width: 46.6rem;
  height: 6rem;
}
.p-campaign__detail {
  margin-top: 4rem;
  letter-spacing: -0.03em;
  line-height: 1.36;
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
}
@media (max-width: 576px) {
  .p-campaign__detail {
    font-size: 5vw;
  }
}
.p-campaign__chart {
  margin: 2.3rem auto 0;
}
.p-campaign__note {
  font-size: 1.6rem;
  margin-top: 2.3rem;
  color: #231815;
  font-weight: 500;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: -0.03em;
  text-align: left;
}
.p-campaign__note :last-child {
  margin-top: 2.4rem;
}
@media (max-width: 576px) {
  .p-campaign__note {
    font-size: 1.1rem;
  }
}
.p-campaign__list {
  margin: 2.6rem auto 1.5rem;
}
.p-campaign__listItem {
  margin: 0 auto;
  width: 84%;
  object-fit: cover;
  object-position: top;
}
.p-campaign__listItem img {
  width: 100%;
}
@media (max-width: 576px) {
  .p-campaign__listItem {
    width: 100%;
  }
}

.p-reason {
  padding: 7rem 0 4.5rem;
  margin: 0 auto;
  background: #fff7da;
}
.p-reason__wrapper {
  margin-top: 3rem;
}
.p-reason__list {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.p-reason__listCol {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.8rem;
  margin-top: 1.8rem;
}
.p-reason__listCol li {
  max-width: 29rem;
}
.p-reason__note {
  margin-top: 2rem;
  line-height: 1.3;
  font-weight: bold;
}
@media (max-width: 576px) {
  .p-reason__note {
    font-size: 1.1rem;
  }
}

.p-lineup {
  background-color: #fdf7ed;
  padding: 3rem 0 4.4rem;
}
.p-lineup__gridWrapper {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 1.3rem;
}
.p-lineup__gridItem {
  position: relative;
}
.p-lineup__img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(transparent 60%, rgba(0, 0, 0, 0.7));
}
.p-lineup__name {
  font-size: 2.1rem;
  color: #ffffff;
  font-weight: bold;
  position: absolute;
  left: 0;
  bottom: 0;
  user-select: none;
  margin: 0 0 0.5rem 0.5rem;
  line-height: 1.1;
  letter-spacing: 0.01em;
}
@media (max-width: 576px) {
  .p-lineup__name {
    font-size: 3.6vw;
  }
}

.p-voices {
  padding: 6rem 0;
  background: url("../../assets/img/voices-bg@2x-80.jpg") center/cover no-repeat;
}
.p-voices__cardUnit {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  gap: 2.2rem;
}
@media (max-width: 576px) {
  .p-voices__cardUnit {
    gap: 1.6rem;
  }
}

.p-compare {
  padding: 4rem 0 8.1rem;
  margin: 0 auto;
  background: #fdf7ed;
}
.p-compare__content {
  margin-top: 2.5rem;
}

.p-shopping {
  padding: 8rem 0 9rem;
  background: #fff7da;
}
.p-shopping__intro {
  margin-top: 1.4rem;
  text-align: center;
  font-weight: bold;
  font-size: 2.1rem;
  line-height: 1.2;
  letter-spacing: 0.01em;
}
@media (max-width: 576px) {
  .p-shopping__intro {
    font-size: 1.7rem;
  }
}
.p-shopping__content {
  margin-top: 5.3rem;
}

.p-faq {
  padding: 6.6rem 0 0;
}
.p-faq__title {
  text-align: center;
  margin-top: 5.9rem;
  letter-spacing: -0.04em;
  font-size: 4.4rem;
  font-weight: 900;
  line-height: 1;
}
@media (max-width: 576px) {
  .p-faq__title {
    font-size: 8vw;
  }
}
.p-faq__accordion {
  margin-top: 5rem;
}
.p-faq__accordion dl {
  font-weight: 500;
}
.p-faq__accordion dl :first-child {
  margin-top: 0;
}
.p-faq__accordion dt {
  font-size: 2.1rem;
  font-weight: 500;
  min-height: 5.5rem;
  border: 0.75px solid #663300;
  border-radius: 10px 0 0 0;
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
  margin-top: 2.2rem;
  padding: 1.6rem 5.5rem 1.6rem 3rem;
  display: flex;
  position: relative;
}
@media (max-width: 576px) {
  .p-faq__accordion dt {
    font-size: 3.5vw;
    padding: 1rem 2.5rem 1rem 1rem;
    margin-top: 1.4rem;
  }
}
.p-faq__accordion dt ::after {
  content: "";
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  border-left: 2.5px solid #663300;
  border-bottom: 2.5px solid #663300;
  transform: rotate(-45deg) translateY(-50%);
  transition: transform 0.5s;
  position: absolute;
  right: 2.4rem;
  top: 50%;
}
@media (max-width: 576px) {
  .p-faq__accordion dt ::after {
    right: 1rem;
    width: 1.2rem;
    height: 1.2rem;
  }
}
.p-faq__accordion dd {
  padding: 0 3.4rem;
  height: 0;
  line-height: 0;
  overflow: hidden;
  font-size: 1.8rem;
  transition: 0.4s;
  color: #663300;
  opacity: 0;
  border: 0.75px solid transparent;
  border-top: none;
  background: #fdf7ed;
}
@media (max-width: 576px) {
  .p-faq__accordion dd {
    padding: 0 1.6rem;
    font-size: max(1.2rem, 3vw);
  }
}
.p-faq__accordion dt.is-active span::after {
  transform: rotate(135deg);
}
.p-faq__accordion dd.is-open {
  border: 0.75px solid #663300;
  border-top: none;
  line-height: 1.5;
  opacity: 1;
  padding: 3.4rem;
  height: auto;
}
@media (max-width: 576px) {
  .p-faq__accordion dd.is-open {
    padding: 1.6rem;
  }
}
.p-faq__number {
  font-family: "din-2014", "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-weight: 600;
  margin-right: 2rem;
}
@media (max-width: 576px) {
  .p-faq__number {
    margin-right: 1rem;
  }
}

.p-contact {
  padding: 6rem 0 2rem;
  text-align: center;
}
.p-contact__text {
  font-size: 2.4rem;
  font-weight: bold;
}
@media (max-width: 576px) {
  .p-contact__text {
    font-size: 3.7vw;
  }
}
.p-contact__phone {
  font-size: min(9.5vw, 5.7rem);
  letter-spacing: -0.01em;
  line-height: 1;
  margin-top: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "din-2014", "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-weight: 600;
}
.p-contact__phone::before {
  content: "";
  display: block;
  width: min(8.7vw, 5.6rem);
  height: min(8.7vw, 5.6rem);
  background: url("../../assets/img/contact-phone.svg") center/contain no-repeat;
  margin-right: 0.2em;
}
.p-contact__detail {
  font-size: min(2.6vw, 1.7rem);
  font-weight: bold;
  font-family: "Helvetica", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  margin-top: 0.5rem;
  line-height: 1.2;
}
.p-contact__hour {
  font-family: "din-2014", "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-weight: 600;
  font-size: min(3.1vw, 2rem);
}
.p-contact .c-btn {
  margin-top: 1.5rem;
}

