@use '../../foundation/_variable' as *;

.p-contact {
    padding: 6rem 0 2rem;
    text-align: center;
&__text {
    font-size: 2.4rem;
    font-weight: bold;
    @include breakpoint(small) {
        font-size: 3.7vw;
    }
}
&__phone {
    font-size: min(9.5vw, 5.7rem);
    letter-spacing: -0.01em;
    line-height: 1;
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: $font-din;
    font-weight: 600;
    &::before {
        content: "";
        display: block;
        width: min(8.7vw, 5.6rem);
        height: min(8.7vw, 5.6rem);
        background: url("../../assets/img/contact-phone.svg") center / contain no-repeat;
        margin-right: 0.2em;
    }
}
&__detail {
    font-size: min(2.6vw, 1.7rem);
    font-weight: bold;
    font-family: $font-hiragino;
    margin-top: 0.5rem;
    line-height: 1.2;
}
&__hour {
    font-family: $font-din;
    font-weight: 600;
    font-size: min(3.1vw, 2rem);
}
.c-btn {
    margin-top: 1.5rem;
}
}
